    padding: 45px 10% 30px;
  }
}
@media (min-width: 1400px) {
  .result-card {
    padding: 45px 15% 30px;
  }
}
.result-card svg {
  width: fit-content;
  height: 53px;
  object-fit: contain;
  margin-bottom: 13px;
}
@media (min-width: 720px) {
  .result-card svg {
    height: 75px;
  }
}
.result-card p {
  margin: unset;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
}
@media (min-width: 720px) {
  .result-card p {
    font-size: 16px;
    line-height: 20px;
  }
}

.section-consult {
  position: relative;
  z-index: 1;
  padding: 40px 0;
  background: linear-gradient(180deg, #1176ea 0%, rgba(17, 118, 234, 0.8) 100%);
}
@media (min-width: 720px) {
  .section-consult {
    padding: 60px 0;
  }
}
@media (min-width: 1200px) {
  .section-consult {
    padding: 50px 0;
    background-image: linear-gradient(195deg, var(--blue) 0%, #1b66bd 100%);
  }
}
.section-consult h2 {
  color: #ffffff;
}
.section-consult .consult-form {
  padding: 0 8.335%;
}
@media screen and (max-width: 1199.98px) {
  .section-consult .consult-form > *:nth-child(1) {
    order: -1;
  }
  .section-consult .consult-form > *:nth-child(2) {
    order: 1;
  }
  .section-consult .consult-form > *:nth-child(3) {
    order: 5;
  }
  .section-consult .consult-form > *:nth-child(4) {
    order: 0;
  }
  .section-consult .consult-form > *:nth-child(5) {
    order: 4;
  }
  .section-consult .consult-form > *:nth-child(6) {
    order: 5;
  }
  .section-consult .consult-form > *:nth-child(7) {
    order: 6;
  }
}
@media (min-width: 720px) {
  .section-consult .consult-form {
    padding: 0 16.665%;
  }
}
@media (min-width: 1200px) {
  .section-consult .consult-form {
    padding: 0;
  }
}

.consult-form {
  display: flex;
  flex-direction: column;
  column-gap: 5px;
  row-gap: 5px;
  margin-top: 24px;
}
@media (min-width: 720px) {
  .consult-form {
    column-gap: 12px;
    row-gap: 12px;
    margin-top: 40px;
  }
}
@media (min-width: 1200px) {
  .consult-form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
    column-gap: 8px;
    row-gap: 8px;
  }
  .consult-form > *:nth-child(1) {
    grid-area: 1/1/2/2;
  }
  .consult-form > *:nth-child(2) {
    grid-area: 1/2/2/3;
  }
  .consult-form > *:nth-child(3) {
    grid-area: 1/3/3/4;
  }
  .consult-form > *:nth-child(4) {
    grid-area: 2/1/3/2;
  }
  .consult-form > *:nth-child(5) {
    grid-area: 2/2/3/3;
  }
  .consult-form > *:nth-child(6) {
    grid-area: 3/2/4/4;
  }
}
.consult-form .input-textarea {
  resize: none;
}
@media (min-width: 1200px) {
  .consult-form-agree {
    align-self: center;
  }
}
.consult-form-agree * {
  color: #ffffff;
}
.consult-form-link {
  text-decoration: underline;
  text-decoration-skip-ink: none;
}
.consult-form-link:hover {
  text-decoration: none;
}
.consult-form .btn {
  width: fit-content;
  padding: 16px 23%;
  margin: auto;
}
@media (min-width: 720px) {
  .consult-form .btn {
    padding: 20px 20%;
  }
}
@media (min-width: 1200px) {
  .consult-form .btn {
    margin: unset;
    padding: 20px 136px;
  }
}
.consult-form-submit {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  column-gap: 15px;
  row-gap: 15px;
  margin-top: 15px;
}
@media (min-width: 720px) {
  .consult-form-submit {
    column-gap: 35px;
    row-gap: 35px;
    margin-top: 7px;
  }
}
@media (min-width: 1200px) {
  .consult-form-submit {
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    column-gap: 24px;
    row-gap: 24px;
    margin-top: 16px;
  }
}